            "\"content\":\"媒体4月10日称，比利时人力资源公司SD Worx遭到网络攻击，被迫关闭其英国和爱尔兰的IT基础设施。SD Worx给英国和爱尔兰客户的通知称，他们在托管数据中心发现恶意活动，已采取行动并隔离了所有系统和服务器。该公司针对其它欧洲国家的登录网站仍然运行正常，但英国的网站无法访问。没有关于此次攻击类型的详细信息，有人担心敏感数据在攻击期间被盗。作为一家人力资源和薪资公司，SD Worx为其客户的员工管理着大量敏感数据，如税务信息、身份证号码和银行帐号等。\"," +
