            "\"content\":\"媒体4月8日报道，Cyfirma发布关于新威胁组织ARES Leaks的分析报告。自BreachedForum关闭以来，ARES Leaks活动有所增加，表明在不久的将来它有可能成为备选方案之一。OSINT搜索发现ARES Group的管理员出售零日漏洞，表明该组织正在利用漏洞来攻击坏系统。该组织由渗透测试人员和恶意软件开发者等资源组成。除了数据泄露外，它还提供僵尸网络和DDoS服务。ARES还表现出类似cartel的行为，积极寻求与其他攻击者的联系。\",\"title\":\"Cyfirma发布关于ARES Leaks运营、发展和能力的分析\",\"source\":\"启明星辰集团ADLab\"," +
            "\"url\":\"https://www.cyfirma.com/outofband/ares-leaks-emerging-cyber-crime-cartel/\",\"create_time\":\"2023-04-11T11:57:51+08:00\"},{\"id\":\"6f5598433f1e2f0a9d0aae5e86954377\",\"content\":\"据4月6日报道，勒索团伙Medusa声称攻击了塞浦路斯开放大学(OUC)。OUC是位于塞浦路斯尼科西亚的一所在线大学，提供远程学习。上周，该大学发布了一份关于3月27日发生的网络攻击的公告，此次攻击导致多个中央服务和关键系统宕机。4月6日，Medusa在网站上列出了OUC并勒索100000美元，留给该机构14天的时间。该团伙还发布了被盗数据样本，涉及学生名单和承包商的财务细节等。\",\"title\":\"塞浦路斯开放大学OUC遭到勒索团伙Medusa的攻击\",\"source\":\"启明星辰集团ADLab\",\"url\":\"https://www.bleepingcomputer.com/news/security/medusa-ransomware-claims-attack-on-open-university-of-cyprus/\",\"create_time\":\"2023-04-11T11:57:51+08:00\"},{\"id\":\"6e7f70b7ef3a6259ee7a6e3edef6732f\",\"content\":\"Sucuri在4月6日披露了自2017年以来一直攻击WordPress网站的大规模Balada Injector活动。Sucuri称，Balada Injector攻击大约每月发生一次，每次攻击都使用新注册的域名来绕过拦截名单。通常，恶意软件会利用所有已知和最近发现的主题和插件漏洞，主要注入Linux后门。Sucuri观察到的注入方法包括siteurl hack、HTML注入、数据库注入和任意文件上传。研究人员估计，超过100万个WordPress网站已被此活动感染。\",\"title\":\"Sucuri披露针对WP网站的大规模Balad Injector活动\",\"source\":\"启明星辰集团ADLab\",\"url\":\"https://blog.sucuri.net/2023/04/balada-injector-synopsis-of-a-massive-ongoing-wordpress-malware-campaign.html\",\"create_time\":\"2023-04-11T11:57:51+08:00\"},{\"id\":\"eff1d3af063a1308a66d868ba6a5122c\"," +
            "\"content\":\"据媒体4月7日报道，攻击者在一个俄语的黑客论坛上发布了一则广告，声称要出售加拿大网络运营商Rogers Communications的数据库。其中包括Rogers的3个活动目录（AD）数据库：users、groups和devices。通常，AD包括有关公司环境的关键数据。Rogers证实，该公司的部分数据在暗网上泄露，然而泄露的数据库中仅包含员工数据，没有客户的详细信息。这3个数据库的标价为14000美元，没有具体说明数据库的大小或它所公开\"}]",
